import React from "react";
import Layout from "../../containers/layout";
import { graphql } from "gatsby";
import ReactHtmlParser, {
  processNodes,
  convertNodeToElement,
  htmlparser2,
} from "html-react-parser";

import {
  Text,
  Avatar,
  CardHeader,
  Stack,
  Image,
  Anchor,
  Box,
  Heading,
  Card,
  CardBody,
  CardFooter,
} from "grommet";

const Home = ({ dataList }) => {
  const avatarSrc = "";
  return (
    <Box direction="row" wrap={true} justify="center">
      {dataList &&
        dataList.map((e, i) => {
          const { thumbnail, title, videoId, cta, description } = e;
          return (
            <Box key={i} pad="small">
              <Card elevation="large" width="small">
                <Stack anchor="bottom-left">
                  {thumbnail && (
                    <CardBody height="small">
                      <Image fit="cover" src={thumbnail} a11yTitle="bridge" />
                    </CardBody>
                  )}
                  {avatarSrc && (
                    <CardHeader
                      // https://gist.github.com/lopspower/03fb1cc0ac9f32ef38f4#all-hex-value-from-100-to-0-alpha
                      background="#000000A0"
                      width="small"
                      align="center"
                     
                    >
                      <Avatar src={avatarSrc} a11yTitle="avatar" />
                      <Box>
                        <Text size="small">$10</Text>
                      </Box>
                    </CardHeader>
                  )}
                </Stack>
                <Box fill={true} justify="center" alignContent="center" align="center">
                <Heading level="4" margin={{ vertical: "small" }}>
                  {title}
                </Heading>
                <Heading level="5">
                  {description && ReactHtmlParser(description || "")}
                </Heading>
                </Box>
                <CardFooter align="center">
                  <Box fill={true} justify="center" alignContent="center" align="center">{cta}</Box>
                </CardFooter>
              </Card>
            </Box>
          );
        })}
    </Box>
  );
};

export default Home;
